import React from "react";
import classes from "./Dialogs.module.css";
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Message/Message";
import AddNewMessageReduxForm from "./DialogsForm";

const Dialogs = (props) => {
  let state = props.dialogsPage;

  let dialogElements = state.dialogsData.map((dialog) => (
    <DialogItem name={dialog.name} key={dialog.id} id={dialog.id} />
  ));

  let messageElements = state.messagesData.map((message) => (
    <Message message={message.message} key={message.id} id={message.id} />
  ));

  let onSendMessage = (values) => {
    props.sendMessage(values.newMessage);
  };

  return (
    <div>
      <div className={classes.dialogs}>
        <div className={classes.dialogs_items}>{dialogElements}</div>
        <div className={classes.messages}>
          <div className={classes.message_item}>{messageElements}</div>
        </div>
      </div>
      <div className={classes.type_message_block}>
        <AddNewMessageReduxForm onSubmit={onSendMessage} />
      </div>
    </div>
  );
};

export default Dialogs;
